.App {
  text-align: center;
}

.side_menu{
  width: 25%;
  float: left;
  background-color: #fff;
  padding: 20px;
  border: 10px solid #c1c1c1;
  
}
.menu_data{
  width: 73%;
  float: left;
  background-color: #fff;
  padding: 20px;
  margin-left: 2%;
  border: 10px solid #c1c1c1;
  
}
.list-group-item{
  border: none;
}
.container{
  background-color: ##fffff;
  position: fixed;
  height: 100%;
  overflow: auto;
  width: 100%;
  float: left;

}

ul {
  list-style-type: none;
  margin: 0;
 }

li a {
  display: block;
  color: #000;
  padding: 8px 16px;
  text-decoration: none;
}

li a.active {
  background-color: #04AA6D;
  color: white;
}

li a:hover:not(.active) {
  background-color: #555;
  color: white;
  text-decoration: none;
}

.rdt_TableHeadRow{
  font-weight: bold;
}